export default {
	data() {
		return {
			footerArrs: [
				{
					label: '返回',
					icon: 'scan-back'
				},
				{
					label: '扫码',
					icon: 'scan-scan'
				},
				{
					label: '转换',
					icon: 'scan-trans'
				}
			]
		}
	},
	methods: {
		clickHandle(data) {
			switch(data.icon) {
				case 'scan-back': {
					uni.navigateTo({
					    url: `/pages/other/main?tabsName=scan`
					});	
					break;
				}
				case 'scan-scan': {
					this.scanHandle();
					break;
				}
				case 'scan-trans': {
					break;
				}
				default: break;
			}
		},
		scanHandle() {
			uni.scanCode({
			    scanType: ['barCode'],
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
			    }
			});
		}
	}
}
